	/**
	 * shallow mix, returns new object
	 */
	function mix (o1, o2, etc) {
		var o = {};
		for (var i = 0; i <= arguments.length; i++){
			var oN = arguments[i];
			if(oN){
				Object.keys(oN).forEach(function (key) {
					o[key] = oN[key];
				});
			}
		}
		return o;
	}

